                    .more 
                        a(href="" class="btn hvr-sweep-to-bottom demo") Request demo
                        a(href="" class="btn hvr-sweep-to-bottom offer") Request offer
        Footer
</template>

<script>
import { mapState } from "vuex";
// @ is an alias to /src
import Header from "@/components/global/Header.vue";
import Footer from "@/components/global/Footer.vue";

export default {
    name: "OurSolutions",
    components: {
        Header,
        Footer,
    },
    mounted() {
        this.$store.dispatch("getSolutionsData");
    },
    computed: {
        ...mapState(["SolutionsData"]),
    },
